import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructurePagingWarehouse } from './structure-paging.warehouse';
import { StructureId } from '../../domain/structure.id';
import { NextPageEvent } from '../../domain/paging/next-page/next-page.event';
import { PrevPageEvent } from '../../domain/paging/prev-page/prev-page.event';
import { PageChangedEvent } from '../../domain/paging/page/page-changed.event';
import { Paging } from './read/paging';
import { PagesizeChangedEvent } from '../../domain/paging/change-pagesize/pagesize-changed.event';


@Injectable()
export class StructurePagingEventRepository {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly pagingReadModelService: StructurePagingWarehouse) {
	}

	onPageChange(structureId: StructureId): Observable<number> {

		return this.domainEventBus
				   .ofEvent(
					   NextPageEvent,
					   PrevPageEvent,
					   PageChangedEvent
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingReadModelService
									  .onPaging(structureId)
									  .pipe(
										  map((paging: Paging) => paging.getPage()),
										  take(1)
									  );
					   })
				   );
	}

	onPageSizeChange(structureId: StructureId): Observable<number> {

		return this.domainEventBus
				   .ofEvent(
					   PagesizeChangedEvent
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingReadModelService
									  .onPaging(structureId)
									  .pipe(
										  map((paging: Paging) => paging.getPageSize()),
										  take(1)
									  );
					   })
				   );
	}

}
